@import url(https://fonts.googleapis.com/css?family=Nova+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#F5F5F5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#nav-bar-holder {
    width:100%;
    left:0;
    right:0;
    top:0;
    background-color: white;
}

#left-nav-bar {
    float:left;
    width: 60%;
    padding:1%;
}

.nav-link-styles {
    padding:2%;
    text-decoration: none;
    color:black;
}

.nav-link-styles:hover {
    background-color:gray;
}

.active {
    background-color:#ed9985;
    color:lightgray;
    font-weight:bold;
}

#blog_title {
    font-family: 'Nova Script', cursive;
    text-align: center;
    font-size:2em;
    color:#ed9985;
}

#title_container {
    width:100%;
    left:0;
    right:0;
    margin-top:8%;
}

#main-body-container {
    width:82%;
    margin-left:9%;
    margin-top: 8%;
}

#left-home-container {
    float: left;
    width:70%;
}

#right-home-container {
    float:right;
    width:30%;
}

.card-body {
    width:80%; 
    margin-left:10%;
    margin-right:10%;
    margin-bottom:5%;
    background-color:white;
    padding:2%;  
}

#about-me {
    width:90%;
    margin-left:5%;
    margin-right:5%;
    margin-bottom:5%;
    padding: 2%;
    background-color:white;
}

#blog_prof_pic {
    width:100%;
}

.title-text {
    font-family: 'Nova Script', cursive;
    text-align:center;
    color:#ed9985;
}

#current_months {
    width:90%;
    margin-left:5%;
    margin-right:5%;
    padding: 2%;
    background-color:white;
}
.min-card-body {
    width:38%;
    margin-right: 5%;
    margin-bottom: 5%;
    float:left;
    background-color:white;
    padding:2%;
}

.icon-style-contact {
    margin-right:3%;
    z-index: -90;
}
.post_container {
    width:90%;
    margin-left:5%;
    margin-right:5%;
    margin-bottom:5%;
    background-color:white;
    padding:2%;
    word-wrap: break-word;
}
